/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Arial', sans-serif;
}

/* Container */
body {
  background-color: #f4f7fa;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  color: #333;
  font-size: 16px;
}

/* Main content wrapper */
div {
  background-color: #fff;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 800px;
  margin: 0 20px;
}

/* Header */
h1 {
  font-size: 36px;
  text-align: center;
  margin-bottom: 20px;
  color: #4CAF50;
}

/* Sub-header */
h2 {
  font-size: 28px;
  margin-bottom: 10px;
  color: #333;
}

/* Input fields */
input[type="text"],
input[type="number"] {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  box-sizing: border-box;
}

/* Buttons */
button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

/* Game list */
ul {
  list-style-type: none;
  margin: 20px 0;
  padding: 0;
}

li {
  background-color: #f9f9f9;
  padding: 10px;
  margin: 5px 0;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

li button {
  background-color: #008CBA;
}

li button:hover {
  background-color: #007B9E;
}

/* Rules Section */
h3 {
  margin-top: 20px;
  color: #4CAF50;
  font-size: 22px;
}

div > div {
  margin-bottom: 15px;
}

/* Number Display */
ul li {
  font-size: 18px;
  padding: 10px 0;
}

ul li span {
  font-weight: bold;
  color: #333;
}

ul li span.correct {
  color: green;
}

ul li span.incorrect {
  color: red;
}

/* Create Game Section */
.create-game {
  margin-bottom: 20px;
}

.create-game input,
.create-game button {
  margin-bottom: 10px;
}

.create-game h3 {
  color: #4CAF50;
}

.range {
  display: flex;
  justify-content: space-between;
}

.range input {
  width: 48%;
}

.add-rule-btn {
  background-color: #FF5722;
  margin-top: 10px;
}

.add-rule-btn:hover {
  background-color: #E64A19;
}
